import React, { Component } from 'react';
import './components.css';
import {Drawer} from "antd";
import ReactMarkdown from "react-markdown";



class Cooked extends Component {

  constructor(props){
    super(props);
    this.state = {
        visible: false,
        terms: null
    }
  }

  componentWillMount(){
    const readmePath = require("../blogs/mrx.md");

    fetch(readmePath).then((response) => response.text()).then(text =>{
      this.setState({terms:text})
    })
  }

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    //const {caption} = this.state;


    const pic = require("../pics/mrx.jpg");
    return (
        <div className="navItemContainer">
        <button className="smallName navitem" onClick={this.showDrawer}>
            Cooked
        </button>
        <Drawer
          title="Cooked"
          placement="left"
          closable={true}
          width = "55%"
          onClose={this.onClose}
          visible={this.state.visible}
         
        >
          <div className="blog">
            <div className="title">
                Fishbowl's Mr X.
            </div>
            <img src={pic} className="picture"/>
            <div className="date"><i>Written by Bryan Moh || 1.4.2019</i></div>
            <ReactMarkdown source={this.state.terms} className="blogtext"/>
          </div>
        </Drawer>

        </div>
    )
  }
}

export default Cooked;
